<template>
  <div class="main-page">
    <nav class="links">
      <new-scenario-button></new-scenario-button>
      <history-button></history-button>
    </nav>
    <!--<h2><span>Scnarios disponibles</span> <list-filter mode="ALL" v-on:changeFilter="changeFilter($event)"></list-filter></h2>-->
    <h2><span>Scnarios disponibles</span> <list-filter mode="ALL" v-on:changeFilter="filterMode=$event"></list-filter></h2>
    <div class="scenario-list">
      <scenario-item v-for="item in ScenarioList" v-if="filter(item, filterMode)" :scenario="item" :key="item.id" @deleteScenario="deleteScenario"></scenario-item>
    </div>
  </div>
</template>

<script>
import ScenarioItem from 'components/ScenarioItem';
import NewScenarioButton from 'components/buttons/NewScenarioButton';
import HistoryButton from 'components/buttons/HistoryButton';
import ListFilter from 'components/mainPage/ListFilter';
import ScenarioList from 'model/ScenarioList';

export default {
  name: 'main-page',
  data () {
	  let logged = this.$root.$data.loggedIn
	  if(!logged) this.$router.push('/login');
    return {
      filterMode: 'ALL',
      ScenarioList : logged ? ScenarioList.get() :[],
    }
  },
  components: {
    ScenarioItem, NewScenarioButton, HistoryButton, ListFilter
  },
  methods: {
    filter : function(item, fmode) {
      switch(fmode){
        case 'ALL': return true; break;
        case 'SONO': return item.author == 'Sonovision'; break;
        case 'MINE' : return item.author == this.$root.$data.username; break;
      }
    },
    deleteScenario : function(scenario){
      this.deleteConfirmed.scenario = scenario;
      this.$emit("confirm", "voulez-vous supprimer le scnario "+scenario.name+" ?", this.deleteConfirmed);
    },
    deleteConfirmed : function(confirmed){
      if (confirmed) {
        ScenarioList.deleteScenario(this.deleteConfirmed.scenario.id);
      }
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
  font-size: 1.2rem;
  border-bottom: 1px solid black;
  /*font-variant: small-caps;*/
  text-transform: uppercase;
  padding-bottom: 0.25rem;
}
h2 {
  display: flex;
  align-items: flex-end;
}
h2 span:first-child {
  flex:1;
}
nav.links >* {
  display: inline-block;
  font-size: 1.2rem;
  margin-top: 1em;
  margin-right: 1em;
}
</style>



