<template>
  <div class="edit-scenario-page">
    <h2>Modifier le scnario</h2>
    <scenario-item-edited :scenario="scenario"></scenario-item-edited>
    <h2>Tches</h2>
    <div class="task-view">
      <div class="task-list">
        <template v-for="(task, index) in tasks">
          <add-task-button 
          class="atb" 
          v-if="index>0"
          v-on:click="addTask(index)"></add-task-button>
          <task-item 
          :task="task" 
          :isActive="activeTask.id==task.id"
          :delitable="index != 0 && index != (tasks.length - 1)"
          v-on:editTask="editTask"
          v-on:deleteTask="deleteTask"></task-item>
        </template>
        <!--<p> debug {{tasks}}</p>-->
      </div>
      <div class="task-parameters">
        <div class="pannel">
          <h3 class="task-name"><input v-model="activeTask.name" type="text"></input></h3>
          <select-parameter 
          :param="{id:'taskType', label:'Type de tche'}"
          :choices="taskTypes"
          v-model="activeTask.type || 'generic'"
          @input="v => activeTaskEditor=getTaskEditor(v)"
          ></select-parameter>
        </div>
        <component :is="activeTaskEditor"
        v-if="activeTask.type!='undefined'"
        :task="activeTask"></component>
      </div>
    </div>
    <generic-button @click="nextbutton">ENREGISTRER LE SCENARIO</generic-button>
  </div>
</template>

<script>
import Config from 'config';
import rest from 'lib/rest';
import GenericButton from 'components/buttons/GenericButton';
import ScenarioItemEdited from 'components/edit/ScenarioItemEdited';
import TaskItem from 'components/edit/TaskItem';
import AddTaskButton from 'components/edit/AddTaskButton';
import GenericTaskEditor from 'components/edit/taskEditors/GenericTaskEditor';
import CustomConversionTaskEditor from 'components/edit/taskEditors/CustomConversionTaskEditor';
import SelectParameter from 'components/parameters/SelectParameter';
import ScenarioList from 'model/ScenarioList';
import ScenarioTasks from 'model/ScenarioTasks';
import TaskConfig from 'model/TaskConfigs';
import Task from 'model/Task.class';
import Scenario from 'model/Scenario.class';
import ApplicationError from 'lib/errors';

export default {
  name: 'edit-scenario-page',
  data () {
    return {
    	editData: {},
    	tasks: [],
      activeTask: new Task(),
      activeTaskEditor: GenericTaskEditor,
      scenarioSaved: false,
      scenario: new Scenario(),
      /*activeTaskType: ScenarioTasks.getScenarioTasks(this.$route.params.sid)[0].type,*/
    };
  },

  computed: {
    /*scenario: function(){
      if(this.$route.params.sid != undefined) { return ScenarioList.getById(this.$route.params.sid); }
      else return ScenarioList.getCurrentEdited();
    },*/
    taskTypes: function(){
      return Object.keys(TaskConfig);
    },
  },
  
  created: function(){
		rest.get(Config.REST.GET_SCENARIO_TASKS.replace('{id}',this.$route.params.sid),(success, xhr)=>{
			if(success){
				this.editData = JSON.parse(xhr.responseText);
				this.scenario.load(this.editData);
				this.tasks.splice(0,this.tasks.length);
				for(let prop in this.editData.taskList){ 
					this.tasks.push(new Task().load(this.editData.taskList[prop]));
				}
			} else {
				
			}
		});	  
  },

  methods: {
	  
    //slection d'une tche
    editTask : function(task){
      this.activeTask = task;
      this.activeTaskEditor = this.getTaskEditor(task.type);
    },

    //cration d'une nouvelle tche
    addTask : function(index){
      /*ScenarioTasks.createNewTask(this.scenario.id, index);
      this.scenario.steps ++;*/
    },

    //suppression d'une tche
    deleteTask : function(task){
      // si la tche supprime est la tche active, on active la tche prcdente
      /*if(this.activeTask == task) this.activeTask = this.tasks[this.tasks.indexOf(task)-1];
      ScenarioTasks.deleteTask(this.scenario.id, task);
      this.scenario.steps --;*/
    },

    //enregistrement du scnario
    nextbutton: function(){
  		
    	let exportTasks = [];
    	for(let task in this.tasks){
    		exportTasks.push(this.tasks[task].save());
    	}
    	this.editData = this.scenario.save();
    	this.editData.taskList = exportTasks;

      rest.submitEdit(this.editData, (success, xhr)=>{
    	  if(success){
    		  this.$emit('alert', 'Scenario enregistr.');
    	  } else {
    		  this.$emit('errorAlert', new ApplicationError(ApplicationError.saveScenarioFailed));
    	  }
      });  
    },

    exitConfirmed: function(confirmed){
      confirmed ? this.exitConfirmed.next() : this.exitConfirmed.next(false);
    },

    //renvoie l'diteur correspondant au type de la tche active
    getTaskEditor : function(newtype){
      var result = "";
      switch(newtype){
        case 'customConv': result = 'CustomConversionTaskEditor'; break;
        default : result = 'GenericTaskEditor';
      }
      console.log("change task editor to: "+result);
      return result;
    },
  },

  beforeRouteLeave: function(to, from, next){
    if (this.scenarioSaved) next();
    else {
      this.exitConfirmed.next = next;
      this.$emit("confirm", "Ce scnario n'est pas enregistr.\nVoulez-vous quitter ?", this.exitConfirmed);      
    }
  },
  
  /*beforeRouteEnter: function(to, from, next){
	  ScenarioTasks.clearEdit();
	  ScenarioList.clearEdit();
  },*/


  components: {
    GenericButton, ScenarioItemEdited, TaskItem, AddTaskButton, GenericTaskEditor, SelectParameter, CustomConversionTaskEditor,
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
  font-size: 1.2rem;
  border-bottom: 1px solid black;
  /*font-variant: small-caps;*/
  text-transform: uppercase;
  padding-bottom: 0.25rem;
}
h3 {
  margin: -0.5rem 0 0.8rem 0;
  padding: 0 0 0.3rem 0;
  font-weight: bold;
  color: #334754;
  font-size: 1rem;
  border-bottom: 1px solid gray;
}
.task-view {
  justify-content: space-between;
  display: flex;
  width:100%;
}
.task-parameters {
  width:68%;
  margin: 0 0 1rem 0;
}
.task-list {
  width:28%;
  margin: 0 0 1rem 0;
}
.pannel {
  background-color: white;
  border: 1px solid #334754;
  border-bottom: 3px solid #334754;
  border-right: 2px solid #334754;
  padding: 1rem;
  margin: 1rem 0;
}
.atb {
  width: 1.5rem;
  height: 1.5rem;
  margin: 0 auto;
}
.task-name input {
  font:inherit;
  border: 1px dotted white;
  width: 100%;
  box-sizing: border-box;
  margin: 0;
}
.task-name input:hover {
  border: 1px dotted gray;
}
</style>



