import Vue from 'vue'
import Router from 'vue-router'
import MainPage from 'components/MainPage'
import LoginPage from 'components/LoginPage'
import NotAvailablePage from 'components/NotAvailablePage'
import SetupPage from 'components/run/SetupPage'
import NewScenarioPage from 'components/new/NewScenarioPage'
import EditScenarioPage from 'components/edit/EditScenarioPage'
import ExecutionPage from 'components/run/ExecutionPage'
import ExecutionResultPage from 'components/run/ExecutionResultPage'
import HistoryPage from 'components/history/HistoryPage'

Vue.use(Router)

let appRouter = new Router({
  routes: [
    { // Accueuil - liste des scenarios
      path: '/',
      name: 'Main',
      component: MainPage
    },
    { // login - formulaire de connexion
      path: '/login',
      name: 'Login',
      component: LoginPage
    },
    { // paramtrer un scenario avant son execution
      path: '/scenario/:sid/setup',
      name: 'ScenarioSetup',
      component: SetupPage
    },
    { // lancer l'execution d'un scenario
      path: '/scenario/:sid/run/:execId',
      name: 'ScenarioRun',
      component: ExecutionPage
    },
    { // rsultat de l'execution d'un scenario
      path: '/scenario/:sid/result/:execId',
      name: 'ScenarioResult',
      component: ExecutionResultPage
    },
    { // modification des tches d'un scenario
      path: '/scenario/edit/:sid',
      name: 'ScenarioEditExisting',
      component: EditScenarioPage
    },
    { // modification des tches d'un scenario
      path: '/scenario/edit',
      name: 'ScenarioEdit',
      component: EditScenarioPage
    },
    { // cration d'un nouveau scenario
      path: '/scenario/new',
      name: 'ScenarioNew',
      component: NewScenarioPage
    },
    { // historique des executions d'un scenario dtermin
      path: '/history/:sid',
      name: 'HistoryFor',
      component: HistoryPage
    },
    { // historique des executions de tous les scenarios
      path: '/history',
      name: 'History',
      component: HistoryPage
    },
    { // page d'erreur ou page introuvable
      path: '/notavailable',
      name: 'Error',
      component: NotAvailablePage
    }
  ]
});

export default appRouter;



