<template>
<div class="generic-button" v-on:click="trigger($event)"><slot>GENERIC BUTTON</slot></div>
</template>

<script>
export default {
	name: 'generic-editor-button',
	methods: {
		trigger: function(event){
			this.$emit('click', event);
		}
	}
}
</script>

<style scoped>
div {
	font-family: Arial;
	font-size: 10pt;
	font-weight: bold;
	color: black;
	text-align: center;
	text-transform: uppercase;
	display: inline-block;
	box-sizing: border-box;
	cursor:pointer;
	padding: 0.5em 1em;
	min-width: 10em;
	background-color: white;
	border: 1px solid black;
}
div:hover {
	background-color: #ffb619;
	color: white; border-color: white;
}
div:active {
	background-color: #334754;
	color: white; border-color: white;
}
</style>


