'use strict'

/*------- storage -------------*/
var taskconfigsStorage = {};

addConfig('generic', 'Gnrique', []);
addConfig('load', 'chargement', [new Parameter('inputFile', 'file', "Fichier d'entre")]);
addConfig('save', 'enregistrement', [new Parameter('outputFile', 'file', "Fichier de sortie")]);
addConfig('customConv', 'conversion personnalise', 
	[new Parameter('inputDTD', 'addOrSelectFile', "DTD d'entre"),
	new Parameter('outputDTD', 'addOrSelectFile', "DTD de sortie"),
	new Parameter('conversionTable', 'xmlConversionTable', "Table de conversion")],
	);

export default taskconfigsStorage;

/*------- classes --------------*/

/* classe TaskConfig
*/
function TaskConfig(type, name, parameters, editable, setupable){
	// type de la tche (load, save, ect...)
	this.taskType = type || "";
	// nom par dfaut de la tche ("chargement", "enregistrement", ect...)
	this.taskDefaultName = name || "";
	// la tche peut tre modifie dans l'diteur (dfaut = true)
	this.editable = editable || editable === false ? false : true;
	// la tche peut tre configure avant la conversion (dfaut = true)
	this.setupable = setupable || setupable === false ? false : true;
	// les paramtres par dfaut de la tche
	this.taskDefaultParameters = parameters || [];
}

/* classe Parameter
*/
function Parameter(id, type, label, editable, setupable){
	this.id = id || NaN;
	this.label = label || "";
	this.type = type || "";
	this.editable = editable || editable === false ? false : true;
	this.setupable = setupable || setupable === false ? false : true;
}

/* modle de paramtre : {id:'codifFile', label:'Fichier de codification', inputType:'file'}*/
/* modle de tche : {id:0, name:"chargement", type:"load", parameters:[]}*/

/*-------- utilitaires ----------*/

// cre et ajoute une config au storage
function addConfig(type, name, parameters){
	taskconfigsStorage[type] = new TaskConfig(type, name, parameters);
}


