<template>
  <div class="history-page">
    <h2>
    <span v-if="unique">Historique des excutions de {{scenarioName}}</span>
    <span v-else>Historique des excutions</span> 
    <list-filter v-if="!unique" mode="ALL" v-on:changeFilter="filterMode=$event"></list-filter></h2>
    <div class="scenario-list">
      <scenario-item 
      v-for="(item, index) in scenarios" 
      v-if="filter(item, filterMode)" 
      :key="index"
      :scenario="item" 
      :executions="item.executions"
      :open="true"></scenario-item>
      <p v-if="scenarios.length === 0">Aucune excution enregistre. <a href="#" @click.prevent="$router.back()">[retour]</a></p>
      <div v-else>
        <span class="legend">Lgende : </span>
        <span class="legend ok"><i></i>conversions russies</span>
        <span class="legend warning"><i></i>erreurs non bloquantes</span>
        <span class="legend error"><i></i>erreurs bloquantes</span>
      </div>
    </div>
  </div>
</template>

<script>
import rest from 'lib/rest';
import Config from 'config';
import ScenarioItem from 'components/history/ScenarioHistoryItem';
import ListFilter from 'components/mainPage/ListFilter';
import ScenarioList from 'model/ScenarioList';
import ExecutionList from 'model/ExecutionList';
import Scenario from 'model/Scenario.class';

export default {
  name: 'main-page',
  data () {
    return {
      filterMode: 'ALL',
      unique: this.$route.params.sid !== undefined,
      scenarios : (this.$route.params.sid !== undefined) ?
        this.getExecutionsByScenario(this.$root.$data.username, this.$route.params.sid):
        this.getExecutions(this.$root.$data.username),
      scenarioName : '...',
    }
  },
  
  components: {
    ScenarioItem, ListFilter
  },
  
  methods: {
	  
  
	  getExecutions : function(username){
		  	
			rest.get(Config.REST.GET_EXECUTIONS_BY_AUTHOR.replace('{username}',username), (success, xhr)=>{
				if(success){
					let data = JSON.parse(xhr.responseText);
					for(let i=0; i<data.length; i++){
						let scn = new Scenario();
						this.$set(this.scenarios, i, scn.load(data[i]));
					}
				}
			});			
			return [];
	  },
	  
	  getExecutionsByScenario : function(username, sid){
			rest.get(Config.REST.GET_EXECUTIONS_BY_AUTHOR.replace('{username}',username), (success, xhr)=>{
				if(success){
					let data = JSON.parse(xhr.responseText);
					for(let i=0; i<data.length; i++){
						if(data[i].id !== sid) continue;
						let scn = new Scenario();
						this.scenarios.push(scn.load(data[i]));
						this.scenarioName = this.scenarios[0].name;
					}
				}
			});			
			return [];		  
	  },

    filter : function(item, fmode) {
      switch(fmode){
        case 'ALL'  : return true; break;
        case 'SONO' : return item.author == 'Sonovision'; break;
        case 'MINE' : return item.author == this.$root.$data.username; break;
      }
    },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
  font-size: 1.2rem;
  border-bottom: 1px solid black;
  /*font-variant: small-caps;*/
  text-transform: uppercase;
  padding-bottom: 0.25rem;
}
h2 {
  display: flex;
  align-items: flex-end;
}
h2 span:first-child {
  flex:1;
}
nav.links >* {
  display: inline-block;
  font-size: 1.2rem;
  margin-top: 1em;
  margin-right: 1em;
}
.legend {
  margin-right: 1em;
}
.legend i {
  display: inline-block;
  width: 1em;
  height: 1em;
  margin-right: 0.25em;
  transform: translateY(15%);
  background: url(../../assets/pictograms/error-counter.png) no-repeat;
  background-size: 300% 100%;
}
.legend.ok i {
  background-position: left;
}
.legend.warning i {
  background-position: center;
}
.legend.error i {
  background-position: right;
}
</style>



