<template>
<div class="history-button">
	<div class="button-icon"></div>
	<router-link to="/history">Historique des executions</router-link>
</div>
</template>

<script>
export default {
	name: 'history-button'
};
</script>

<style scoped>
.history-button:hover .button-icon {
	background-position: 50%;
}
.history-button:active .button-icon {
	background-position: 100%;
}
.button-icon {
	display: inline-block;
	width: 1.2em;
	height: 1.2em;
	background-image: url(../../assets/buttons/history-button.png);
}
a {
	position: relative;
	top: -0.2em;
	left: 0.2em;
}
</style>


