var ScenarioListData = [];
var currentEdited = null;

import ScenarioTasks from 'model/ScenarioTasks';
import Parameter from 'lib/utils';
import rest from 'lib/rest';
import Vue from 'vue';
import Config from 'config';
import Scenario from 'model/Scenario.class';

export default {
	
	//rcupre les donnes. Si possible depuis le cache
	//@forceUpdate: force une mise  jour depuis le serveur
	get(forceUpdate=false) {
		if(forceUpdate || ScenarioListData.length==0) this.update();
		return ScenarioListData;
	},
	//force la mise  jour depuis le serveur
	update() {
		let that = this;
		function handler(success, xhr){
			ScenarioListData.splice(0, ScenarioListData.length);
			let data = JSON.parse(xhr.responseText);
			for(let i=0;i<data.length;i++){
				let sce = new Scenario({}).load(data[i]);
				ScenarioListData.push(sce);
			}
		}
		rest.get(Config.REST.GET_SCENARIOS, handler);		
	},
	getById(id) {
		if (Parameter.validate("ScenarioId", id, "string")){
			for(let s of ScenarioListData){
				if (s.id == id) return s;
			}
			throw new Error('Request Error : No scenario found for id='+id);
			return null;		
		}
	},
	getCurrentEdited() {
		if(currentEdited){
			return currentEdited;
		} else { throw new Error("No scenario being edited"); }
	},
	setCurrentEdited(scenario) {
		currentEdited = scenario;
	},
	clearEdit() {
		currentEdited = null;
	},
	//renvoi true|false selon la russite de l'opration
	saveScenario() {
		var ces = this.getCurrentEdited();
		ces = JSON.stringify(ces.save());
		rest.post(Config.REST.POST_SCENARIO_SAVE, ces, function(success, xhr){
			if(success){
				return true;
			} else {
				return false;
			}
		});
	},
	deleteScenario(id){
		rest.deleteRequest(`/openconv/rest/conv/scenario/${id}/delete`, (success, xhr)=>{
			if(success){
				this.update();
				return true;
			} else {
				return false;
			}
		});
	},
};


