<template>
  <div class="main-page">
    <!--<h2><span>Scnarios disponibles</span> <list-filter mode="ALL" v-on:changeFilter="changeFilter($event)"></list-filter></h2>-->
    <h2>Excution du scnario</h2>
    <div class="scenario-list">
      <scenario-item :scenario="scenario" :options="{isEditable:false}"></scenario-item>
    </div>
    <h2>Paramtrage</h2>
    <div class="parameters">
      <form 
      id="setupform" enctype="multipart/form-data" method="post"
      v-on:submit.prevent="execButton">
        <div v-for="task in tasks" class="pannel">
          <h3>{{task.name}}</h3>
          <parameter
          v-for="param in task.parameters" 
          :param="param" 
          :value="param.value"
          :key="param.id"
          @input="(val)=>{param.value=val}"
          @fileSelected="appendFile"></parameter>
        </div>
      </form>
    </div>
    <button type="submit" form="setupform"><generic-button>EXECUTER</generic-button></button>
  </div>
</template>

<script>
import ScenarioItem from 'components/ScenarioItem';
import GenericButton from 'components/buttons/GenericButton';
import ScenarioList from 'model/ScenarioList';
import ExecutionList from 'model/ExecutionList';
import ScenarioTasks from 'model/ScenarioTasks';
import Parameter from 'components/parameters/Parameter';
import rest from 'lib/rest';
import ApplicationError from 'lib/errors';

export default {
  name: 'setup-page',
  data () {
    return {
      // scenario this.$route.params.sid
      scenario : ScenarioList.getById(this.$route.params.sid),

      tasks: ScenarioTasks.getSetupTaskList(this.$route.params.sid),
      //renvoi "file" et "listParam"
      
      /*fileCount:0,
      formdata: new FormData(),*/
    };
  },

  methods: {
    
    appendFile: function(fileparam, files){
    	/* un fichier a t ajout */
    },
	  
    execButton: function(event){
      
      //preparation du json et envoi avec fichier
  		let paramList = [];
      let fileIndex = 0;
      let form = document.forms['setupform'];
      
      var formdata = new FormData();
      
      //boucle sur les paramtres pour traiter les fichiers
  		for(let task in this.tasks){
  			for(let param in this.tasks[task].parameters){
  				let paramObj = this.tasks[task].parameters[param];
  				if(paramObj.type.toLowerCase()=="file"){
  					formdata.append('files', form[paramObj.code].files[0]);
  					paramObj.value = fileIndex.toString();
  					fileIndex ++;
  				}
  				paramList.push(paramObj.save());
  			}
  		}

      formdata.append('listParam', JSON.stringify(paramList));
      rest.submitSetup(formdata, this.scenario.id, this.submitHandler);      
    },

    submitHandler: function(succeed, xhr){
      if (succeed) {
        let execId = xhr.responseText;
    	  this.$router.push({name:'ScenarioRun', params:{sid:this.scenario.id, execId:execId}});
      }
      else {
    	  let err = new ApplicationError('networkError');
    	  this.$emit("error", err.fill({status:xhr.status,statusText:xhr.statusText}));
      }
    },
  },

  // composants
  components: {
    ScenarioItem,
    GenericButton,
    Parameter, 
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
  font-size: 1.2rem;
  border-bottom: 1px solid black;
  /*font-variant: small-caps;*/
  text-transform: uppercase;
  padding-bottom: 0.25rem;
}
h3 {
  margin: -0.5rem 0 0.8rem 0;
  padding: 0 0 0.3rem 0;
  font-weight: bold;
  color: #334754;
  font-size: 1rem;
  border-bottom: 1px solid gray;
}
.pannel {
  background-color: white;
  border: 1px solid #334754;
  border-bottom: 3px solid #334754;
  border-right: 2px solid #334754;
  padding: 1rem;
  margin: 1rem 0;
}
button[form="setupform"]{
  padding:0;
}
</style>



