<template>
  <div class="scenario-executions">
    <div class="top-line">
      <p class="date">Executions</p>
      <ul class="links">
        <li><span class="inactive-link">[Tlcharger le log (PDF)]</span></li>
        <li><span class="inactive-link">[Tlcharger les fichiers (ZIP)]</span></li>
      </ul>
    </div>
    <table>
      <tr v-for="exe in executions">
      <td class="date">{{exe.dateEnd.toLocaleString()}}</td>
      <td class="generated">{{exe.generatedNumbers}} fichiers</td>
      <td class="counter ok"><i></i>{{exe.checkedNumber}}</td>
      <td class="counter warning"><i></i>{{exe.warningsNumber}}</td>
      <td class="counter error"><i></i>{{exe.errorsNumber}}</td>
      <td><span class="inactive-link">[Tlcharger les fichiers (ZIP)]</span></td>
      </tr>
    </table>
  </div>
</template>

<script>
export default {
  name: 'scenario-executions',
  props: ['executions'],
  data () {
    return {
      //description: 'description du scenario'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.scenario-executions {
  box-sizing: border-box;
  background-color: white;
  border-bottom: 3px solid #334754;
  border-right: 2px solid #334754;
  border-left: 1px solid #334754;
}
table {
  margin: 0 2rem 0.5rem;
  border-collapse: collapse;
  width: calc(100% - 4rem);
}
tr {
  border-top: 1px solid rgb(128,128,128);
}
td {
  padding: 0.25em 0;
}
td.date {
  width: 20%;
}
td.generated {
  width: 20%;
  border-left: 1px dotted #ff7200;
  padding-left: 1rem;
}
td.counter {
  width: 7%;
  vertical-align: middle;
}
td.counter i {
  display: inline-block;
  width: 1em;
  height: 1em;
  margin-right: 0.25em;
  transform: translateY(15%);
  background: url(../../assets/pictograms/error-counter.png) no-repeat;
  background-size: 300% 100%;
}
td.counter.warning  i {
  background-position: center;
}
td.counter.error i {
  background-position: right;
}
.top-line {
  padding:0.4rem;
  display: flex;
  font-size: 0.9rem;
}
p.date {
  flex:1;
  margin:0 0 0 1.6rem;
}
.links {
  margin:0; padding:0;
  list-style: none;
}
.links li {
  display: inline;
}
.inactive-link {
  opacity: 0.5;
}

</style>



