'use strict'

let Parameter = {

	ALLOW_NULL : 1,
	ALLOW_UNDEFINED : 2,
	ALLOW_NAN : 4,

	// valide un argument de fonction
	validate(paramName, paramValue, typesAllowed, options){
		
		if (! (typesAllowed instanceof Array)) typesAllowed = [typesAllowed];
		if (!options) options = 0;

		if ( paramValue === null ) {
			if ( options&ALLOW_NULL ) return true;
			else throw new TypeError(getError('NullError'));
		}

		else if ( typeof paramValue === undefined ) {
			if ( options&UNDEFINED_ALLOWED ) return true;
			else throw new TypeError(getError('UndefinedError'));
		}

		//else if ()

		else if ( typesAllowed.indexOf(typeof paramValue) != -1){
			return true;
		} else throw new TypeError(getError());

		return false;

		function getError(type){ 
			switch(type){
				case 'NullError': return "Invalid parameter: @"+paramName+". [null] given, null not allowed"; break;
				case 'UndefinedError': return "Invalid parameter: @"+paramName+". [undefined] given, undefined not allowed"; break;
				default : return "Invalid parameter: @"+paramName+". ["+typeof paramValue+"] given, "+typesAllowed+" attempted.";
			}
		}
	}

};

export default Parameter;


