<template>
  <div class="scenario-item"
  v-on:click="toggleDescription()">
    <div class="item-box">
      <div class="creator">
        <img v-if="scenario.author=='Sonovision'" src="static/sono-icon.png">
        <img v-else src="static/user-icon.png">
        <p class="creator-label">cre par</p>
        <p class="creator-name">{{scenario.author}}</p>
      </div>
      <div class="steps"><p>{{scenario.steps}}</p><p>tapes</p></div>
      <div class="name">
        <input v-model="scenario.name" type="text"></input>
      </div>
      <div class="transition"></div>
      <div class="main-button">
        <!--<router-link :to="{name:'ScenarioSetup', params:{sid:scenario.id}}">
          <execute-button class="button"></execute-button>
        </router-link>-->
      </div>
    </div>
    <scenario-description 
    :class="{hidden:!descriptionVisible}" 
    :scenario="scenario" :isEditable="isEditable"></scenario-description>
  </div>
</template>


<!-- ******************************* JS *********************************** -->
<script>
import ScenarioDescription from 'components/edit/ScenarioDescriptionEdited';
export default {
  name: 'scenario-item-edited',
  props: ['scenario', 'options'],
  data () {
    return {
      descriptionVisible: false,
    };
  },
  computed: {
    isEditable: function(){ // Dtermine si le bouton "modifier" est prsent et actif
      if(this.options && this.options.isEditable!==null) {
        return this.options.isEditable && true;
      } else { return this.scenario.author == this.$root.$data.username; }
    },
  },
  methods: {
    toggleDescription : function(){  // Affiche ou masque la zone de description
      this.descriptionVisible = !this.descriptionVisible;
    }
  },
  components : {
    ScenarioDescription,
  }
}
</script>

<!-- ******************************* CSS *********************************** -->
<style scoped>
.scenario-item {
  margin-bottom: 1rem;
  cursor: pointer;
}
.scenario-item .item-box {
  background-color: white;
  border-bottom: 3px solid #ff7200;
  display: flex;
}
.creator {
  width: 20%;
  margin: auto 0 auto 0;
  font-size: 0.9em;
}
.creator p {
 margin: 0;
}
.creator .creator-label {
  color: #334754;
  margin-top: 0.5rem;
}
.creator img {
  margin: 0.5rem;
  float:left;
  width: 2.2em;
  height: 2.2em;
  border-radius: 1.75rem;
}
.creator-name {
  font-weight: bold;
}
.steps {
  text-align: center;
  margin: auto 1rem auto 0;
}
.steps p{
  margin:0;
  color: #334754;
  font-size: 0.9em;
}
.name {
  flex: 1;
  margin: 1rem 0 0.7em 0;
  border-left: 1px solid #ff7200;
}
.name input {
  border: 1px dotted white;
  width: 100%;
  box-sizing: border-box;
  margin: 0;
  margin-left: 0.5rem;
  font-size: 1.2em;
  padding-left: 0.5em;
}
.name input:hover {
  border: 1px dotted gray;
}
h1, h2 {
  font-weight: normal;
  font-size: 1.2rem;
  border-bottom: 1px solid black;
  text-transform: uppercase;
  padding-bottom: 0.25rem;
}
.scenario-description {
  overflow: hidden;
  box-sizing: border-box;
  max-height: 50vh;
  transition: max-height 0.5s, opacity 0.5s ease;
  -webkit-transition: max-height 0.5s, opacity 0.5s ease;
}
.scenario-description.hidden {
  opacity: 0;
  max-height: 0;
  transition: max-height 0.5s, opacity 0.5s ease;
  -webkit-transition: max-height 0.5s, opacity 0.5s ease;
}
.buttons {
}
.button {
  margin-top: 0.5em;
  width:2.20em;
  height:2.20em;
}
.main-button {
  position: relative;
  background-color: #ff7200;
  width: 4em;
}
.transition{
  width: 4em;
  transform: skewX(-30deg) translateX(2.5em);
  background-color: #ff7200;
  z-index: 0;
}
</style>



