<template>
   <component
      :is="getParameterComponent(param.type)" 
      :param="param" 
      :value="value"
      @input="input"
      @fileSelected="fileSelected"></component>
</template>

<script>
import SelectParameter from 'components/parameters/SelectParameter';
import TextParameter from 'components/parameters/TextParameter';
import FileParameter from 'components/parameters/FileParameter';

export default {
	
  name: 'parameter',
  
  props: ['param', 'value'],
  
  methods: {
    
    getParameterComponent: function(type){
      switch(type.toLowerCase()){
        case 'select' : return SelectParameter; break;
        case 'file': return FileParameter; break;
        case 'txt':
        case 'text':
        default : return TextParameter;
      }
    },
    
    input: function(...args){
    	this.$emit('input', args);
    },
    
    fileSelected: function(...args){
    	this.$emit('fileSelected', args);
    } 
    
  },
  
  components: {
	  SelectParameter,
	  TextParameter,
	  FileParameter,
  }
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



