<template>
  <div class="file-parameter">
    <label :for="param.id">{{param.label}}</label>
    <input
     :id="param.id" 
     value=""
     @change="fileSelected($event.target)"
     type="file" 
     :name="param.code" />
  </div>
</template>

<script>
export default {
  name: 'file-parameter',
  props: ['param', 'value'],
  data () {
    return {
      //description: 'description du scenario'
    }
  },
  methods: {
	  fileSelected: function(element){
      this.$emit('fileSelected', this.param, element.files);
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.file-parameter {
  margin-bottom: 0.5rem;
  display: flex;
  border-bottom: dotted 1px #333333;
}
label {
  flex: 1;
  font-size: 0.9em;
}
input, textarea {
  flex: 2;
}
</style>



