<template>
  <input type="text" name="tag" :value="value" @input="updateValue($event.target.value)"/>
</template>

<script>
export default {
  name: 'attribute-item',
  props: ['value'],
  data () {
    return {
      //description: 'description du scenario'
    }
  },
  methods: {
    updateValue: function(value){
      this.$emit('input', value.trim());
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.parameter {
  margin-bottom: 0.4rem;
  display: flex;
  border-bottom: dotted 1px #333333;
}
input, input:hover, input:active, input:focus {
  background: url("../../../assets/components/attribute-icon.png") no-repeat;
  padding-left: 3em;
  background-size: 1em;
  background-position: 1.7em 50%;
  font: inherit;
  border: none;
  outline: none;
}
</style>



