<template>
<div>
	<div :class="{'button-icon':true, 'active':active}" @click="trigger($event)"></div>
</div>
</template>

<script>
require('../../assets/buttons/buttonStyle.css');
export default {
	name: 'edit-task-button',
	props: ['active'],
	methods: {
		trigger(event){
			this.$emit('click', event);
		},
	},
}
</script>

<style scoped>
.button-icon {
	background-image: url('../../assets/buttons/edit-button.png');
}
.button-icon.active {
	background-position: top center;
}
</style>


