<template>
  <div :class="{active:progress}"></div>
</template>

<script>
export default {
  name: 'infinite-loading-bar',
  props: ['progress'],
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@keyframes loading {
  from {
    background-position: 0 0;
  }
  to {
    background-position: 100% 0;
  }
} 
div {
  /*background-color: rgb(237,237,237);*/
  background-color: white;
  background-image: url(../../assets/components/InfiniteLoadingBar-background.png);
  background-size: auto 100%;
  background-repeat: repeat-x;
  margin:0.2em 0;
  /*border: 1px solid #334754;*/
  border-radius: 0.5em;
  height:1em;
}
div.active {
  animation: 30s linear infinite loading;
}
</style>


