<template>
  <div class="error-counter">
    <span class="checked"><i></i>{{checked}}</span>
    <span class="warning"><i></i>{{warnings}}</span>
    <span class="error"><i></i>{{errors}}</span>
  </div>
</template>

<script>
export default {
  name: 'error-counter',
  props: ['checked', 'warnings', 'errors'],
  data () {
    return {
      //description: 'description du scenario'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.error-counter {
  display: inline;
  font-weight: normal;
}
.error-counter>span {
  display: inline-flex;
  align-items: center;
  margin: 0 0.5rem;
}
.error-counter>span i {
  background-image: url('../../assets/pictograms/error-counter.png');
  width: 1rem;
  height: 1rem;
  margin-right: 0.3rem;
  background-repeat: no-repeat;
  background-size: 300% 100%;
}
.checked i{
  background-position: left top;
}
.warning i{
   background-position: center top;
}
.error i{
   background-position: right top;
}
</style>


