<template>
	<div class="login-area">
    <!--<img class="user-icon" src="../assets/user-icon.svg" alt="user picture">-->
    <img class="avatar" :src="avatar" alt="avatar utilisateur">
    <p v-if="$root.$data.loggedIn">Connect en tant que <span class="username">{{this.$root.$data.username}}</span><a id="logout" href="logout" @click.prevent="logout">[Dconnexion]</a></p>
    <p v-else>Dconnect</p>
  </div>
</template>

<script>

import router from 'router/index.js';
import rest from 'lib/rest';
import Config from 'config';

export default {
  name: 'login-area',
  data () {
    return {
      username: this.$root.$data.username,
      avatar: 'static/user-icon.png',
    }
  },
  methods: {
  	logout: function(){
  		this.$emit("confirm", "Souhaitez-vous vous dconnecter ?", this.logoutConfirmed);
  	},
  	logoutConfirmed: function(confirmed){ 		
  		if (confirmed) {
  			rest.get(Config.REST.GET_LOGOUT, (success, xhr)=>{
  				if(success){
  	  	  	this.$root.$emit("loggedOut");
  				}
  			});
  		}
  	},
  }
}
</script>

<style scoped>
	.login-area {
		flex: 1;
		display: flex;
		flex-direction: row-reverse;
		color: #334754;
	}
	p {
		margin: auto 0;
		text-align: right;
	}
	.username {
		display: block;
		font-weight: bold;
		font-style: italic;
		font-size: 1.2em;
	}
	.avatar {
		width: 3rem;
		height: 3rem;
		margin: auto 1rem;
	}
	#logout {
		position: absolute;
		right: 5.5rem;
	}
	.login-area:not(:hover) #logout {
		display: none;
	}
</style>


