<template>
<div>
	<div class="button-icon" @click="onclick"></div>
</div>
</template>

<script>
require('../../assets/buttons/buttonStyle.css');
export default {
	name: 'delete-button',
	methods: {
		onclick: function(){
			this.$emit('click');
		}
	}
}
</script>

<style scoped>
.button-icon {
	background-image: url('../../assets/buttons/delete-button.png');
}
</style>


