<template>
  <form class="bg" novalidate @submit.prevent="close">
    <div class="window">
      <section>
        <p><img src="../../assets/pictograms/warning-icon.png"/></p>
        <p>{{message}}</p>
      </section>
      <footer><button type="submit" class="btn" autofocus><gbutton>ok</gbutton></button></footer>
    </div>
  </form>
</template>

<script>
import gbutton from 'components/buttons/GenericEditorButton';

export default {
  name: 'generic-window',
  props: ["message"],
  methods: {
    close : function(){
      this.$emit('close');
    }
  },
  components: { gbutton },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.bg {
  width: 100%;
  height: 100%;
  position: absolute;
  top:0%;
  left:0%;
  background-color: rgba(0,0,0,0.5);
}
.window {
  display: flex;
  flex-direction: column;
  max-width: 80%;
  max-height: 80%;
  width: 25em;
  margin: 3em auto auto;
  background-color: white;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.4);
}
section {
  font-family: Helvetica;
  text-align: center;
  flex-grow: 1;
}
img {
  width: 5em;
  height: 5em;
}
footer {
  padding: 1em;
  display: flex;
  align-items: center;
  justify-content: center;
}
.btn {
  -webkit-appearance:none;
  -moz-appearance:none;
  appearance:none;
  padding:0;
}
</style>


