'use strict'

export default class {

	constructor(props={}){
		this.code = props.code || "";
		this.name = props.code || "";
		this.type = props.type || "TEXT";
		this.label = props.label || "Paramtre";
		this.description = props.descr || "";
		this.value = props.value || "";
		this.taskId = props.taskId || "";
	}
	
	//charge depuis json
	load(data){
		this.code = data.code || "";
		this.name = data.code || "";
		this.type = data.type || "TEXT";
		this.label= data.label || "Paramtre";
		this.description = data.descr || "";
		this.value = data.value || "";
		this.taskId = data.id.taskId || "";
		return this;
	}
	
	//converti en objet json
	save(){			
		return {
			code:this.code,
			type:this.type,
			label:this.label,
			descr:this.description,
			value:this.value,
			id:{taskId:this.taskId, parameterName:this.code},
		}
	}
	
};


