<template>
  <div class="generic-task-editor">
    <div class="pannel">
      <h3>Paramtres de la tche : {{task.name}} ({{task.type}})</h3>
      <parameter
        v-for="param in task.parameters"
        :key="param.code"
        :param="param" 
        :value="param.value"
        @input="(val)=>{param.value=val}"></parameter>
    </div>
  </div>
</template>

<script>
import Parameter from 'components/parameters/Parameter';
import ScenarioTasks from 'model/ScenarioTasks';
import TaskConfig from 'model/TaskConfigs';

export default {
  name: 'generic-task-editor',

  props: ['task'],

  components : {
    Parameter,
  },

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h3 {
  margin: -0.5rem 0 0.8rem 0;
  padding: 0 0 0.3rem 0;
  font-weight: bold;
  color: #334754;
  font-size: 1rem;
  border-bottom: 1px solid gray;
}
.pannel {
  background-color: white;
  border: 1px solid #334754;
  border-bottom: 3px solid #334754;
  border-right: 2px solid #334754;
  padding: 1rem;
  margin: 1rem 0;
}
</style>


