'use strict'

class ApplicationError extends Error {
	
	static strings = {
		unknownError:"Une erreur non identifie est survenue.",
		invalidLogin:"Identifiants invalides",
		saveScenarioFailed:"L'enregistrement du scnario  chou",
		deleteScenarioFailed:"La suppression du scnario  chou",
		resultNotAvailable:"Fichers convertis non disponibles",
		networkError:"Erreur de connexion au serveur : Code {status}, {statusText}.",
	};

	// classe qui contient les messages d'erreur
	constructor(name, ...args){
		super(name, args);
		this.name = name;
		this.message = ApplicationError.getErrorMessage(name);
		this.fill = function(messageVariables){
			for(let messvar in messageVariables){
				this.message = this.message.replace('{'+messvar+'}', messageVariables[messvar]);
			}
			return this;		
		};
	}

	static getErrorMessage(name){
		if(!this.strings.hasOwnProperty(name)) return ApplicationError.strings.unknownError;
		else return ApplicationError.strings[name];
	}

};

export default ApplicationError;


