<template>
<div class="new-scenario-button">
	<div class="button-icon"></div>
	<router-link to="/scenario/new">Nouveau Scnario</router-link>
</div>
</template>

<script>
export default {
	name: 'new-scenario-button'
};
</script>

<style scoped>
.new-scenario-button:hover .button-icon {
	background-position: 50%;
}
.new-scenario-button:active .button-icon {
	background-position: 100%;
}
.button-icon {
	display: inline-block;
	width: 1.2em;
	height: 1.2em;
	background-image: url(../../assets/buttons/add-button.png);
}
a {
	position: relative;
	top: -0.2em;
	left: 0.2em;
}
</style>


