<template>
  <div class="tag-group">
    <div class="row tag">
      <div class="col">
        <tag-item v-model="inputs[0]"></tag-item>
        <a class="btn add" href="#" @click.prevent="addAttribute"></a>
        <a class="btn remove" href="#" @click.prevent="removeAttribute(0)"></a>
      </div>
      <div class="col">
        <tag-item v-model="outputs[0]"></tag-item>
        <progress v-if="relevance[0]" name="fiability" :value="relevance[0]"/>
      </div>
    </div>
    <div v-for="(input, index) in inputs" v-if="index>0" class="row attribute">
      <div class="col">
        <attribute-item v-model="inputs[index]"></attribute-item>
        <a class="btn add" href="#" @click.prevent="addAttribute"></a>
        <a class="btn remove" href="#" @click.prevent="removeAttribute(index)"></a>
      </div>
      <div class="col">
        <attribute-item v-model="outputs[index]"></attribute-item>
        <progress v-if="relevance[index]" name="fiability" :value="relevance[index]"/>
      </div>
    </div>
  </div>
</template>

<script>
import tagItem from 'components/parameters/ConversionTable/TagItem';
import attributeItem from 'components/parameters/ConversionTable/AttributeItem';

export default {

  name: 'tag-group-item',

  //tableau des entres, tableau des sorties, pertinence de l'association
  props: ['inputs', 'outputs', 'relevance'],

  data () {
    return {
      //description: 'description du scenario'
    }
  },
  methods: {

    addAttribute: function(){
      this.inputs.push('input-attribute');
      this.outputs.push('output-attribute');
      this.relevance.push(0);
    },
    removeAttribute: function(index){
      if(index == 0) { this.$emit('removeTag'); } 
      else {
        this.inputs.splice(index, 1);
        this.outputs.splice(index, 1);
        this.relevance.splice(index, 1);
      }
    }

  },
  components: {
    tagItem, attributeItem,
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.tag-group{
  border-bottom: 1px dotted #B0B0B0;
}
.row {
  width: 100%;
  display: table;
  border-collapse: collapse;
}
.row:hover {
  background-color: #f6f6f6;
}
.col {
  display: inline-flex;
  width: 49%;
}/*
.tag .col:first-child>*:first-child{
  width:77%;
}
.attribute .col:first-child>*:first-child{
  width:70%;
}*/
.col>*:first-child {
  flex-grow: 1;
  margin-right: 1em;
}
.btn {
  position: relative;
  margin-right:0.25em;
  top: 0.1em;
  opacity: 1;
  display: inline-block;
  width: 1em;
  height:1em;
  background-size: 3em 1em;
  background-repeat: no-repeat;
}
.btn:hover {
  background-position: 50% 0;
}
.btn:active {
  background-position: 100% 0;
}
.add {  background-image: url('../../../assets/buttons/editor-add-button.png');}
.remove {  background-image: url('../../../assets/buttons/editor-remove-button.png');}

.row:not(:hover) .btn{
  visibility: hidden;
}
progress {
  width: 3em;
  height: 0.5em;
  position: relative;
  top: 0.5em;
  border:none;
}
</style>



