'use strict'

var ConversionTableStore = 
[
  {
    inputs : ['input-tag-1', 'input-attribute-1-1', 'input-attribute-1-2'],
    outputs : ['output-tag-1', 'output-attribute-1-1', 'output-attribute-1-2'],
    relevance : [0.5, 0.8, 0.2]
  },
  {
    inputs : ['input-tag-2', 'input-attribute-2'],
    outputs : ['output-tag-2', 'output-attribute-2'],
    relevance : [0.5, 0.8]
  },
  {
    inputs : ['input-tag-3', 'input-attribute-3-1', 'input-attribute-3-2', 'input-attribute-3-3'],
    outputs : ['output-tag-3', 'output-attribute-3-1', 'output-attribute-3-2', 'output-attribute-3-3'],
    relevance : [0.5, 0.8, 0.2, 0.2]
  },
  {
    inputs : ['input-tag-4'],
    outputs : ['output-tag-4'],
    relevance : [0.5]
  },
  {
    inputs : ['input-tag-5'],
    outputs : ['output-tag-5'],
    relevance : [0.5]
  },
];

export default {
	get() {
		return ConversionTableStore;
	},

};


