'use strict'

import Parameter from 'model/Parameter.class';

export default class {

	constructor(props={}){
		this.id = (props.id >= 0) ? id : NaN;
		this.name = props.name || "task";
		this.type = props.type || "undefined";
		this.parameters = props.parameters || [];
	}
	
	//charge depuis json
	load(data){
		this.id = data.id;
		this.name = data.name;
		this.parameters = data.parameterList;
		this.parameters.forEach((item,index,arr)=>{ arr[index] = new Parameter().load(item); });
		this.type = data.taskType;
		return this;
	}
	
	//converti en objet json
	save(){
		let params = [];
		this.parameters.forEach((item,index,arr)=>{ params[index] = item.save(); });
			
		return {
			id:this.id,
			name:this.name,
			parameterList:params,
			taskType:this.type
		}
	}
	
};


