<template>
<div class="generic-button" v-on:click="trigger($event)"><slot>GENERIC BUTTON</slot></div>
</template>

<script>
export default {
	name: 'generic-button',
	methods: {
		trigger: function(event){
			this.$emit('click', event);
		}
	}
}
</script>

<style scoped>
.generic-button {
  -webkit-appearance:none;
  -moz-appearance:none;
  border:none;
	display: inline-block;
	box-sizing: border-box;
	cursor:pointer;
	padding: 0.5rem 5rem 0.4rem 2em;
	background-color: white;
	background-repeat: no-repeat;
	background-image: url('../../assets/buttons/generic-button-bg.png');
	background-position: top right;
	background-size: auto 312%;
	border-bottom: 0.3rem solid #ff940d;
}
.generic-button:hover {
	background-position: 100% 100%;
	border-bottom-color: #ffb619;
}
.generic-button:active {
	background-position: 100% 50%;
	border-bottom-color: #334754;
}
</style>


