<template>
  <div class="list-filter">
    <span>FILTRE : <span v-if="selectedMode=='ALL'">TOUT</span>
    <span v-else-if="selectedMode=='SONO'">SONOVISION</span>
    <span v-else-if="selectedMode=='MINE'">PERSONNEL</span></span>
    <div :class="{'button-icon':true, 'all':true, selected:selectedMode=='ALL'}" v-on:click="select('ALL')"></div>
    <div :class="{'button-icon':true, 'mine':true, selected:selectedMode=='MINE'}" v-on:click="select('MINE')"></div>
    <div :class="{'button-icon':true, 'sono':true, selected:selectedMode=='SONO'}" v-on:click="select('SONO')"></div>
  </div>
</template>


<!-- ******************************* JS *********************************** -->
<script>
require('../../assets/buttons/buttonStyle.css');
export default {
  name: 'list-filter',
  props: ['mode'],
  data () {
    return {
      selectedMode: this.mode
    };
  },
  methods: {
    select: function(choice){
      if(choice!=this.selectedMode) {
        this.selectedMode = choice;
        this.$emit('changeFilter', choice);
      }
    }
  },
  components : {}
}
</script>

<!-- ******************************* CSS *********************************** -->
<style scoped>
.list-filter {
  display: inline;
  font-size: 0.8rem;
}
.button-icon.all {background-image: url('../../assets/buttons/filter-all.png');}
.button-icon.mine {background-image: url('../../assets/buttons/filter-own.png');}
.button-icon.sono {background-image: url('../../assets/buttons/filter-sono.png');}
.button-icon {
  width: 1rem;
  height: 1rem;
  display: inline-block;
  cursor: pointer;
}
.button-icon.selected {
  background-position: 100%;
}
</style>



