<template>
  <div id="app">
    <header>
      <div class="topbar">
        <router-link class="logo" to="/"><img src="./assets/openconv-logo.png" alt="openweb logo"></router-link>
        <login-area @confirm="confirm"></login-area>
      </div>
      <div class="logo-banner">
        <p>An <a href="/portail/home.html">OPENWEB</a> solution by Sonovision group</p>
      </div>
    </header>
    <main>
      <router-view class="page"
      @confirm="confirm"
      @alert="alert"
      @error="errorAlert"></router-view>
      <component :is="popup" :message="popupMessage" @close="closePopup"></component>
    </main>
  </div>
</template>

<script>
import LoginArea from 'components/LoginArea';
import GenericWindow from 'components/common/GenericWindow';
import ConfirmWindow from 'components/common/ConfirmWindow';
require('./assets/mainStyle.css');

export default {
  name: 'app',
  data() {
    return {
      popup: GenericWindow,
      popupMessage: "Welcome",
      popupHandler: null,
    }
  },
  methods : {
    confirm: function(message, handler){
      this.popup = ConfirmWindow;
      this.popupMessage = message || "message";
      this.popupHandler = handler;
    },
    alert: function(message, handler){
      this.popup = GenericWindow;
      this.popupMessage = message || "message";
      this.popupHandler = handler;
    },
    errorAlert: function(error, handler){
      this.popup = GenericWindow;
      this.popupMessage = error.message || "erreur indfinie";
      this.popupHandler = handler;
    },
    closePopup : function(...args){
      if (this.popupHandler) this.popupHandler(...args);
      this.popup = null;
    }
  },
  components : {
    LoginArea,
    GenericWindow,
    ConfirmWindow,
  }
}
</script>

<style>
/*orange fonc : rgb(255,114,0), orange clair : rgb(255,183,26)*/
#app {
  display: flex;
  flex-direction: column;
  height:100vh;
}
.topbar {
  background-color: white;
  padding: 0.5rem;
  display: flex;
  max-width: 100%;
}
.logo-banner {
  background: linear-gradient(to right, #ff7200, #ffb71a);
}
@media (max-height: 564px){
  .logo-banner {
    display: none;
  }
}
.logo-banner p {
  color:white;
  margin:0;
  text-transform: uppercase;
  padding-left: 12rem;
  line-height: 3.5vh;
}
.logo-banner a {
  color: white;
  text-decoration: none;
  font-weight: bold;
}
.logo img{
  margin-bottom: -5%;
  margin-left: 5rem;
  max-height: 12vh;
  min-height: 3rem;
}
.login-area{
  max-height: 12vh;
  min-height: 2rem;
}
.page {
  max-width: 1000px;
  width: calc(100% - 4rem);
  margin: 0 auto 2rem;
}
main {
  position: relative;
  flex-grow: 1;
}
</style>



