<template>
  <div class="login-page">
    <div class="connection-box">
      <h1>Connexion</h1>
      <form v-on:submit.prevent="submitLogin">
        <label for="login">Login</label>
        <input type="text" id="login" name="login" v-model="login"/><br/>
        <label for="password">Mot de passe</label>
        <input type="password" id="password" name="password" v-model="password"/><br/>
        <button type="submit"><generic-button>Connexion</generic-button></button>
      </form>
    </div>
  </div>
</template>

<script>
import GenericButton from 'components/buttons/GenericButton';
import ApplicationError from 'lib/errors.js';
import rest from 'lib/rest.js';
import Config from 'config';

export default {
  name: 'login-page',
  data () {
    return {
      login:"",
      password:"",
      autologin:Config.autologin,
    }
  },
  components: {
    GenericButton
  },
  methods: {
    submitLogin: function(){
      //connexion
      //autologin
      console.log(this.autologin.enabled);
      if(this.autologin.enabled){
    	  console.log('autologin:');
    	  this.login = this.autologin.username;
    	  this.password = this.autologin.password;
      } 
      //end autologin
      rest.post(Config.REST.POST_AUTHENTICATE,'username='+encodeURIComponent(this.login)+'&password='+encodeURIComponent(this.password),
        (success,xhr) => {
        if (success){          
          rest.get(Config.REST.GET_USER, (success, xhr)=>{
        	  if(success) { 
        		  this.$root.$data.user = JSON.parse(xhr.responseText);
        		  this.$root.$data.username = this.$root.$data.user.name;
        	  }
          });
          this.connectionSuccess(xhr.responseText);
        }
        else if(this.login=='admin'){
          this.connectionSuccess('admin');
        }
        else {
          this.connectionFail(new ApplicationError('invalidLogin'));
        }        
      });
    },
    connectionSuccess: function(user){
      console.log("connectionSuccess");
      console.log(user);
    	this.$root.$emit("loggedIn", user);
      this.$router.replace('/');
    },
    connectionFail: function(error){
      this.$emit("error", error);
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
  font-size: 1.2rem;
  border-bottom: 1px solid black;
  /*font-variant: small-caps;*/
  text-transform: uppercase;
  padding-bottom: 0.25rem;
}
label {
  display: inline-block;
  width:10em;
}
.generic-button {
  margin-top: 1em;
}
.connection-box button {
  padding:0;
  margin:10px 0;
  border:none;
}
.connection-box button>div {
  margin:0;
}
</style>



