<template>
  <div class="execution-log">
    <div
    v-for="task in log">
      <h3>{{task.name}}</h3>
      <pre class="log">{{task.xmlLog}}</pre>
    </div>   
  </div>
</template>

<script>
import ErrorCounter from 'components/run/ErrorCounter';

export default {
  name: 'execution-log',
  props: ['log'],

  // composants
  components: {
		ErrorCounter,
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
  font-size: 1.2rem;
  border-bottom: 1px solid black;
  /*font-variant: small-caps;*/
  text-transform: uppercase;
  padding-bottom: 0.25rem;
}
h3 {
  display: inline-flex;
  align-items: center;
  width: 100%;
  margin: 0 0 0.3rem 0;
  padding: 0 0 0.3rem 0;
  color: #334754;
  font-weight: normal;
  font-size: 1rem;
  border-bottom: 1px solid gray;
}
.pannel .error-counter {
  flex-grow: 1;
  text-align: center;
}
.execution-log .log {
  font-family: Courier, Sans-ms;
  font-size:0.9rem;
  word-wrap: break-word;      /* IE 5.5-7 */
  white-space: -moz-pre-wrap; /* Firefox 1.0-2.0 */
  white-space: pre-wrap;      /* current browsers */  
}
</style>



