<template>
  <div class="custom-conversion-task-editor">
    <div class="pannel">
      <h3>Paramtres de la tche : {{task.name}} ({{task.type}})</h3>
      <AddOrSelectFileParameter
      :param="parametersConfig[0]">
      </AddOrSelectFileParameter>
      <AddOrSelectFileParameter
      :param="parametersConfig[1]">
      </AddOrSelectFileParameter>
      <generic-editor-button @click="populateConversionTable">valider</generic-editor-button>
    </div>
    <div class="pannel">
      <conversion-table-parameter
      :param="parametersConfig[2]"></conversion-table-parameter>
    </div>
  </div>
</template>

<script>
import AddOrSelectFileParameter from 'components/parameters/AddOrSelectFileParameter';
import ConversionTableParameter from 'components/parameters/ConversionTableParameter';
import GenericEditorButton from 'components/buttons/GenericEditorButton';
import ScenarioTasks from 'model/ScenarioTasks';
import TaskConfig from 'model/TaskConfigs';

export default {
  name: 'custom-conversion-task-editor',

  props: ['task'],

  computed: {
    parametersConfig: function() {
      return TaskConfig[this.task.type].taskDefaultParameters;
    },
  },

  methods: {
    populateConversionTable: function(){
      //envoi des fichiers si ncessaire, rcupration de la table de conversion
    }
  },

  components : {
    AddOrSelectFileParameter,
    ConversionTableParameter,
    GenericEditorButton,
  },

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h3 {
  margin: -0.5rem 0 0.8rem 0;
  padding: 0 0 0.3rem 0;
  font-weight: bold;
  color: #334754;
  font-size: 1rem;
  border-bottom: 1px solid gray;
}
.pannel {
  background-color: white;
  border: 1px solid #334754;
  border-bottom: 3px solid #334754;
  border-right: 2px solid #334754;
  padding: 1rem;
  margin: 1rem 0;
}
</style>


