// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import rest from 'lib/rest'
import Config from 'config'

/* eslint-disable no-new */
let vue = new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App },
  data : {
  	username: 'Frdric Rebche',
  	loggedIn:false,
  	user:{},
  	roles:{
  		editAllScenarios:true,
  	},
  },
  created : function(){
	  this.$on('loggedIn', (user)=>{
		  this.loggedIn = true;
		  this.$data.username = user;
		  console.log('loggedIn:'+user);
		  console.log(user);
	  });
	  this.$on('loggedOut', (user)=>{
		  this.loggedIn = false;
		  this.$data.username = '';
		  console.log('loggedOut');
	  	  this.$router.push("Login");
	  });
	  
	  //si connect, configure data.user
	  //si deconnect du serveur, route sur cran login
	  rest.get(Config.REST.GET_USER, (success, xhr)=>{
		  if(success){
			  this.$data.user = JSON.parse(xhr.responseText);
			  this.$data.username = this.$data.user.name;
			  this.$data.logggedIn = true;
			  this.$router.push("/");
		  } else {
			  this.$router.push("/login");
		  }
	  });
  }
});

router.beforeEach((to, from, next)=>{
	if(vue.$data.loggedIn || to.name == 'Login') next();
	else next('/login');
});



