<template>
  <div class="main-page">
    <h2>Excution du scenario</h2>
    <div class="scenario-list">
      <scenario-item :scenario="scenario"></scenario-item>
    </div>
    <h2>Excution en cours</h2>
    <div class="pannel">
      <h3>Veuillez patienter... <progress-bar :progress="progress"></progress-bar></h3>
      <execution-log :log="log"></execution-log>
    </div>
  </div>
</template>

<script>
import restAPI from 'lib/rest';
import Config from 'config';

import ScenarioItem from 'components/ScenarioItem';
import Parameter from 'components/run/parameter';
import GenericButton from 'components/buttons/GenericButton';
import ProgressBar from 'components/common/InfiniteLoadingBar';
import ExecutionLog from 'components/run/ExecutionLog';
import ScenarioList from 'model/ScenarioList';
import ExecutionList from 'model/ExecutionList';

var ExecutionPageComponent = {
  name: 'setup-page',
  data () {
    return {

      scenario : //scenarioList.getById(this.$route.params.sid),
        this.getScenario(),

      execution : //ExecutionList.getById(this.$route.params.execId),
        this.getExecution(),

      rest: this.getExecutionState(1500),
      log: this.generateLog(),
      
      progress: true, //la conversion progresse
      
    };
  },

  methods: {

    getScenario: function(){try{return ScenarioList.getById(this.$route.params.sid);}catch(error){this.handleError(error);}},

    getExecution: function(){
    	try{
    		return ExecutionList.getById(this.$route.params.execId).current;
    	}catch(error){this.$router.replace({name:'Error'});}
    },

    getExecutionState: function(interval){
      
      this.intervalID = window.setInterval(()=>{
    	  
    	  this.progress = true;

    	  if (this.execution.status == ExecutionList.STATUS.FINISHED){
      		window.clearInterval(this.intervalID);
      		this.$router.replace({name:'ScenarioResult', params:{sid:this.$route.params.sid, execId:this.$route.params.execId}});
      	} 
    	  
    	  else if(this.execution.status == ExecutionList.STATUS.WAITING){
      		this.progress = false;
      	}
    	  
    	  else {
      		this.execution = this.getExecution();
      		this.generateLog();
      	}

      }, interval);
      
    },

    generateLog : function(){
    	let log = {name:"Loading log...", id:null, xmlLog:"--- LOADING LOG ---"};
 	    return restAPI.get(Config.REST.GET_EXECUTION_LOG.replace('{id}',this.$route.params.execId),(success, xhr)=>{
    	  if(success) this.log = JSON.parse(xhr.responseText);
      });
    	return log;  		
    }
  },

  beforeRouteLeave (to, from, next) {

    if (this.execution.status == ExecutionList.STATUS.FINISHED) {
      next(); 
    }
    else {
      var c = confirm("Attention, l'excution est toujours en cours. Souhaitez-vous abandonner ?");
      if (c) {
    	  window.clearInterval(this.intervalID);
        console.log("Execution annule");
        next();
      } else {
        console.log("Execution toujours en cours");
        next(false);
      }
    }
  },

  // composants
  components: {
    ScenarioItem, ProgressBar, ExecutionLog
  },
}

export default ExecutionPageComponent;
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
  font-size: 1.2rem;
  border-bottom: 1px solid black;
  /*font-variant: small-caps;*/
  text-transform: uppercase;
  padding-bottom: 0.25rem;
}
h3 {
  margin: -0.5rem 0 0.8rem 0;
  padding: 0 0 0.3rem 0;
  font-weight: bold;
  color: #334754;
  font-size: 1rem;
  border-bottom: 1px solid gray;
}
.pannel {
  background-color: white;
  border: 1px solid #334754;
  border-bottom: 3px solid #334754;
  border-right: 2px solid #334754;
  padding: 1rem;
  margin: 1rem 0;
}
.pannel .log {
  font-family: Courier, Sans-ms;
  font-size:0.9rem;
  word-wrap: break-word;      /* IE 5.5-7 */
  white-space: -moz-pre-wrap; /* Firefox 1.0-2.0 */
  white-space: pre-wrap;      /* current browsers */  
}
</style>



