'use strict'

import Execution from 'model/Execution.class';

export default class {
	
	constructor(props){
		if(!props) props = {};
		this.id = props.id || null;
		this.name = props.name || "";
		this.description = props.description || "";
		this.steps = props.steps || 2;
		this.author = props.author || "Lorem Ipsum";
		this.dateCreation = props.dateCreation || new Date();
		this.filename = props.filename || "";
		this.tasks = props.tasks || [];
		this.executions = props.executions || [];
	}
	//charge  partir des donnes JSON
	load(data){
		this.id = data.id;
		this.name = data.name;
		this.description = data.descr;
		this.steps = data.nbSteps;
		this.author = data.creatorName;
		this.dateCreation = new Date(data.creationDate);
		this.filename = data.filename || "";
		this.tasks = data.taskList || [];
		if(data.listHistoryDetailedModel){
			this.executions = [];
			for(let i=0; i < data.listHistoryDetailedModel.length; i++){
				let exec = new Execution();
				this.executions[i] = exec.load(data.listHistoryDetailedModel[i]);
			}
		}
		return this;
	}
	
	//prpare le JSON pour envoi
	save(){
		return {
			id:this.id,
			name:this.name,
			descr:this.description,
			nbSteps:this.steps,
			creatorName:this.author,
			creationDate:this.dateCreation.value,
			filename:this.filename,
			taskList:this.tasks,
			listHistoryDetailedModel:(function(execs){
				if(!execs) return [];
				let result = [];
				for(let i=0; i < execs.length; i++){
					result[i] = execs[i].save();
				}
				return result;
			})(this.executions),
		}
	}
};


