'use strict'
export default {
	
	get ROOT_URL(){
		return window.location.origin; // -> http://localhost:8080
	},

	REST:{
		POST_AUTHENTICATE:'/openconv/login',
		GET_LOGOUT:'/openconv/logout',
		GET_USER:'/openconv/rest/userinfo',
		GET_SCENARIOS:'/openconv/rest/conv/scenario',
		GET_SCENARIO_PARAMETERS:'/openconv/rest/conv/scenario/{id}/setup',
		GET_SCENARIO_TASKS:'/openconv/rest/conv/scenario/{id}/edit',
		POST_SCENARIO_SAVE:'/openconv/rest/conv/scenario/save',
		DELETE_SCENARIO:'/openconv/rest/conv/scenario/{id}/delete',
		POST_SCENARIO_RUN:'/openconv/rest/conv/scenario/{id}/run',
		GET_EXECUTIONS:'/openconv/rest/conv/execution/list',
		GET_EXECUTIONS_BY_AUTHOR:'/openconv/rest/conv/execution/list?username={username}',
		GET_EXECUTION_PROGRESS:'/openconv/rest/conv/execution/{id}/progress',
		GET_EXECUTION_LOG:'/openconv/rest/conv/execution/{id}/log',
		GET_EXECUTION_LOGPDF:'/openconv/rest/conv/execution/{id}/logPDF',
		GET_RESULT:'/openconv/rest/conv/execution/{id}/result',
		POST_SCENARIO_RUN_CONTINUE: '/openconv/rest/conv/execution/{id}/continue',
	},
	
	get CLIENT_SETTINGS(){
		let ls = localStorage.getItem('openconv-launch-settings');
		return ls ? JSON.parse(ls) : {};
	},
	
	get autologin(){
		if(this.CLIENT_SETTINGS.autologin){
			return this.CLIENT_SETTINGS.autologin;
		} else return {enabled:false,username:"",password:""};
	},
	
}


