<template>
  <div class="parameter">
    <label :for="param.id">{{param.label}}</label>
    <textarea v-if="param.type=='textarea'"
     :value="value"
     @input="updateValue($event.target.value)"
     :name="param.id" 
     :id="param.name" 
     cols="30" rows="10"></textarea>  
    <input v-else 
     :id="param.id" 
     :value="value"
     @input="updateValue($event.target.value)"
     :type="type" 
     :name="param.name" />
  </div>
</template>

<script>
export default {
  name: 'parameter',
  props: ['param', 'value'],
  computed:{
	  type: function(){
		  if(this.param.type=="FILE") return "file";
		  else return "text";
	  },
	  value: function(){
		  if(this.param.type=="FILE") return "";
		  else return this.param.value;
	  },
  },
  
  methods: {
  
    updateValue: function(value){
    	if(this.param.name=="inputFolder") return null;
    	this.param.value = value.trim();
      this.$emit('input', value.trim());
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.parameter {
  margin-bottom: 0.5rem;
  display: flex;
  border-bottom: dotted 1px #333333;
}
label {
  flex: 1;
  font-size: 0.9em;
}
input, textarea {
  flex: 2;
}
</style>



