<template>
  <div class="main-page">
    <h2>Excution du scenario</h2>
    <div class="scenario-list">
      <scenario-item-executed 
      :scenario="scenario"
      :options="{zipfile:$route.params.execId}"
      @downloadZip="downloadZip"></scenario-item-executed>
    </div>
    <h2>Excution termine</h2>
    <div class="pannel">
      <h3><span class="title">{{execution.generatedNumbers}} fichiers gnrs ({{dateExecution}})</span>
        <error-counter :checked="execution.checkedNumber" :warnings="execution.warningsNumber" :errors="execution.errorsNumber"></error-counter>
        <a :href="logPDF" download="download">[tlcharger le log (PDF)]</a>
      </h3>
      <execution-log :log="log"></execution-log>
    </div>
  </div>
</template>

<script>
import ScenarioItemExecuted from 'components/run/ScenarioItemExecuted';
import ErrorCounter from 'components/run/ErrorCounter';
import ExecutionLog from 'components/run/ExecutionLog';
import ScenarioList from 'model/ScenarioList';
import ExecutionList from 'model/ExecutionList';
import rest from 'lib/rest';
import Config from 'config';

export default {
  name: 'setup-page',
  data () {
    return {

      scenario : ScenarioList.getById(this.$route.params.sid),

      execution : ExecutionList.getById(this.$route.params.execId).current,

      dateExecution : new Date().toLocaleString(),
      
      log : this.getLog(),
      
      logPDF : Config.REST.GET_EXECUTION_LOGPDF.replace('{id}', this.$route.params.execId),

    };
  },

  methods: {
    downloadPDFLog: function(){
      alert("Log PDF non implment");
    },
  	downloadZip: function(){
  		window.location = Config.REST.GET_RESULT.replace('{id}', this.$route.params.execId);
  	},
    getLog(){
    	let log = {name:"Loading log...", id:null, xmlLog:"--- LOADING LOG ---"};
 	    return rest.get(Config.REST.GET_EXECUTION_LOG.replace('{id}',this.$route.params.execId),(success, xhr)=>{
    	  if(success) this.log = JSON.parse(xhr.responseText);
      });
    	return log;  		
  	},
  },

  // composants
  components: {
    ScenarioItemExecuted, ErrorCounter, ExecutionLog
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
  font-size: 1.2rem;
  border-bottom: 1px solid black;
  /*font-variant: small-caps;*/
  text-transform: uppercase;
  padding-bottom: 0.25rem;
}
h3 {
  display: inline-flex;
  align-items: center;
  width: 100%;
  margin: 0 0 0.3rem 0;
  padding: 0 0 0.3rem 0;
  color: #334754;
  font-weight: normal;
  font-size: 1rem;
  border-bottom: 1px solid gray;
}
.pannel .title {
  font-weight: bold;
  position: relative;
  top: -0.1rem;
}
.pannel h3 a {
  font-size: 0.9rem;
  text-align: right;
  flex-grow: 1;
}
.pannel {
  background-color: white;
  border: 1px solid #334754;
  border-bottom: 3px solid #334754;
  border-right: 2px solid #334754;
  padding: 1rem;
  margin: 1rem 0;
}
.pannel .error-counter {
  flex-grow: 1;
  text-align: center;
}
.pannel .log {
  font-family: Courier, Sans-ms;
  font-size:0.9rem;
  word-wrap: break-word;      /* IE 5.5-7 */
  white-space: -moz-pre-wrap; /* Firefox 1.0-2.0 */
  white-space: pre-wrap;      /* current browsers */  
}
</style>



