'use strict'

export default class {

	constructor(props={}){
		this.scenarioId = props.scenarioId || "";
		this.author = props.author || "";
		this.dateCreation = props.dateCreation || new Date();
		this.status = props.status || "initialized";
		this.dateEnd = props.dateEnd || null;
		this.zipFile = props.zipFile || null;
		this.checkedNumber = props.checkedNumber || 0;
		this.warningsNumber = props.warningsNumber || 0;
		this.errorsNumber = props.errorsNumber || 0;
		this.generatedNumbers = props.generatedNumbers || 0;
	}
	
	json(data){
		if(data) {
			return this.load(data);
		}
		else {
			return this.save();
		}
	}
	
	//charge depuis json
	load(data){
		if(data.idExe!=undefined) this.id = data.idExe;
		if(data.status!=undefined) this.status = data.status;
		if(data.scenarioId!=undefined) this.scenarioId = data.scenarioId;
		if(data.userId!=undefined) this.author = data.userId;
		if(data.startTime!=undefined) this.dateCreation = new Date(data.startTime);
		if(data.endTime!=undefined) this.dateEnd = new Date(data.endTime);
		if(data.nbrOK!=undefined) this.checkedNumber = data.nbrOK;
		if(data.nbrNBloquant!=undefined) this.warningsNumber = data.nbrNBloquant;
		if(data.nbrNOK!=undefined) this.errorsNumber = data.nbrNOK;
		if(data.nbrFiles!=undefined) this.generatedNumbers = data.nbrFiles;
		return this;
	}
	
	//converti en objet json
	save(){			
		return {
			idExe:this.id,
			status:this.status,
			nbrFiles:this.generatedNumbers,
			nbrOK:this.checkedNumber,
			nbrNOK:this.errorsNumber,
			nbrNBloquant:this.warningsNumber,
			startTime:this.dateCreation,
			endTime:this.dateEnd,
			userId:this.author,
			scenarioId:this.scenarioId,
		}
	}
	
};


