'use strict'

var ExecutionListData = [ 
/*
  {id:0, scenarioId: 0, author:"Frdric Rebche", dateCreation:new Date(), status:undefined, dateEnd:new Date(), zipFile:undefined, checkedNumber:10, warningsNumber:11, errorsNumber:12, generatedNumbers:13},
  {id:1, scenarioId: 0, author:"Frdric Rebche", dateCreation:new Date(), status:undefined, dateEnd:new Date(), zipFile:undefined, checkedNumber:10, warningsNumber:11, errorsNumber:12, generatedNumbers:13},
  {id:2, scenarioId: 0, author:"Frdric Rebche", dateCreation:new Date(), status:undefined, dateEnd:new Date(), zipFile:undefined, checkedNumber:10, warningsNumber:11, errorsNumber:12, generatedNumbers:13},
  {id:3, scenarioId: 1, author:"Frdric Rebche", dateCreation:new Date(), status:undefined, dateEnd:new Date(), zipFile:undefined, checkedNumber:8, warningsNumber:2, errorsNumber:3, generatedNumbers:11},
  {id:4, scenarioId: 1, author:"Frdric Rebche", dateCreation:new Date(), status:undefined, dateEnd:new Date(), zipFile:undefined, checkedNumber:8, warningsNumber:2, errorsNumber:3, generatedNumbers:11},
  {id:5, scenarioId: 2, author:"Frdric Rebche", dateCreation:new Date(), status:undefined, dateEnd:new Date(), zipFile:undefined, checkedNumber:10, warningsNumber:11, errorsNumber:12, generatedNumbers:12},
  {id:6, scenarioId: 2, author:"Frdric Rebche", dateCreation:new Date(), status:undefined, dateEnd:new Date(), zipFile:undefined, checkedNumber:10, warningsNumber:11, errorsNumber:12, generatedNumbers:12},
  {id:7, scenarioId: 2, author:"Frdric Rebche", dateCreation:new Date(), status:undefined, dateEnd:new Date(), zipFile:undefined, checkedNumber:10, warningsNumber:11, errorsNumber:12, generatedNumbers:12},
  {id:8, scenarioId: 2, author:"Frdric Rebche", dateCreation:new Date(), status:undefined, dateEnd:new Date(), zipFile:undefined, checkedNumber:10, warningsNumber:11, errorsNumber:12, generatedNumbers:12},
/**/
];

import restAPI from 'lib/rest';
import Execution from 'model/Execution.class';
import Config from 'config';
import Vue from 'Vue';

export default {
	STATUS: {
		INITIALIZED:"INITIALIZED",
		RUNNING:"RUNNING",
		WAITING:"WAITING",
		FINISHED:"FINISHED",
	},

	get() {
		return ExecutionListData;
	},

	getById(id) {
		ExecutionListData.current = new this.Execution();
		restAPI.get(Config.REST.GET_EXECUTION_PROGRESS.replace('{id}', id), (success, xhr)=>{
			if(success){
				ExecutionListData.current.json(JSON.parse(xhr.responseText));
				Vue.set(ExecutionListData, 'current', ExecutionListData.current);
			}
			else {
				throw new Error('Request Error : No execution found for id='+id);
			}
		});
		return ExecutionListData;
	},

	getByAuthor(author){
		/*var eld = this.get();
		var result = {};
		var sid = null;
		for (var exe in eld){
			//on filtre les rsultats par author puis on groupe par scnarioId.
			if(author == eld[exe].author){
				sid = eld[exe].scenarioId;
				if(result[sid]) result[sid].push(eld[exe]);
				else result[sid] = [eld[exe]];
			}
		}
		return result;*/
		restAPI.get(Config.REST.GET_EXECUTIONS_BY_AUTHOR, (success, xhr)=>{
			if(success){
				ExecutionListData.current.json(JSON.parse(xhr.responseText));
				Vue.set(ExecutionListData, 'current', ExecutionListData.current);
			}
			else {
				throw new Error('Request Error : No execution found for id='+id);
			}
		});
	},

	getByAuthorAndScenario(author, sid){
		var eld = this.get();
		var result = {};
		for (var exe in eld){
			//on filtre les rsultats par author et sid.
			if(author == eld[exe].author && sid == eld[exe].scenarioId){
				if(result[sid]) result[sid].push(eld[exe]);
				else result[sid] = [eld[exe]];
			}
		}
		return result;
	},

	updateExecution(exe){
		console.log('[ExecutionList] updateExecution()');
		restAPI.get('/rest/result/1', null, exe.complete);
		return exe;
	},

	// datamodel des executions
	Execution(scenarioId, author){
		return new Execution({scenarioId:scenarioId, author:author});
	},
};


