<template>
  <div class="task-item">
    <div class="task-id">
      <p class="task-name">{{task.name}}</p>
      <p class="task-type">{{task.type}}</p>
    </div>
    <div class="buttons">
      <edit-task-button :active="isActive" v-on:click="setActive"></edit-task-button>
      <delete-task-button v-if="delitable" v-on:click="deleteTask"></delete-task-button>
    </div>
  </div>
</template>

<script>
import EditTaskButton from 'components/edit/EditTaskButton';
import DeleteTaskButton from 'components/edit/DeleteTaskButton';

export default {
  name: 'task-item',
  props: ['task', 'isActive', 'delitable'],
  data () {
    return {
      //description: 'description du scenario'
    }
  },
  components : {
    EditTaskButton, DeleteTaskButton,
  },
  methods : {
    setActive: function(event){
      if(!this.isActive) {
        this.$emit('editTask', this.task);
      }
    },
    deleteTask : function(){
      this.$emit('deleteTask', this.task);
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.task-item {
  font-size: 0.9rem;
  background-color: white;
  border-bottom: 3px solid #ff7200;
  border-right: 3px solid #ff7200;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
.task-id {
  flex:1;
}
.task-id p {
  margin: 0.4rem 0.8rem;
}
.task-name {
  font-weight: bold;
  color: #334754;
}
.task-type {
  font-style: italic;
}
.buttons {
  display: flex;
  margin: 0.5rem;
}
.buttons>* {
  width: 2rem;
  height: 2rem;
  margin-left: 0.5rem;
}
</style>



