'use strict'

import Config from 'config';
import Task from 'model/Task.class';
import Parameter from 'model/Parameter.class';
import rest from 'lib/rest';
import Vue from 'vue';

var ScenarioTasksData = {};
var currentEditedScenarioTasks = null;
var ScenarioTasks = [];
var ScenarioTasksEdit = [];
var ScenarioSetup = {}; //parametrage pour run
var ScenarioEdit = {}; //modification du scenario

export default {

	getSetup(scenarioId) {
		rest.get(Config.REST.GET_SCENARIO_PARAMETERS.replace('{id}',scenarioId),(success, xhr)=>{
			if(success){
				ScenarioSetup = JSON.parse(xhr.responseText);
				ScenarioTasks.splice(0,ScenarioTasks.length);
				for(let prop in ScenarioSetup.taskList){ 
					//ScenarioSetup.taskList[prop] = new Task().load(ScenarioSetup.taskList[prop]);
					//Vue.set(ScenarioSetup.taskList, prop, new Task().load(ScenarioSetup.taskList[prop]));
					ScenarioTasks.push(new Task().load(ScenarioSetup.taskList[prop]));
				}
				console.log(ScenarioSetup);
				Vue.set(ScenarioSetup, 'taskList', ScenarioTasks);
			} else {
				
			}
		});
		return ScenarioSetup;
		
	},
	
	getEdit(scenarioId){
		rest.get(Config.REST.GET_SCENARIO_TASKS.replace('{id}',scenarioId),(success, xhr)=>{
			if(success){
				ScenarioEdit = JSON.parse(xhr.responseText);
				ScenarioTasksEdit.splice(0,ScenarioTasksEdit.length);
				for(let prop in ScenarioEdit.taskList){ 
					ScenarioTasksEdit.push(new Task().load(ScenarioEdit.taskList[prop]));
				}
				console.log(ScenarioEdit);
				Vue.set(ScenarioEdit, 'taskList', ScenarioTasksEdit);
			} else {
				
			}
		});
		return ScenarioEdit;
	},
	
	//liste des tches au setup
	getSetupTaskList(sid){
		if(ScenarioTasks.length==0) this.getSetup(sid);
		return ScenarioTasks;
	},
	
	getEditTasks(sid){
		if(ScenarioTasksEdit.length==0) this.getEdit(sid);
		return ScenarioTasksEdit;		
	},
	
	clearEdit(){
		ScenarioTasksEdit = [];
		ScenarioEdit = {};
	},

	setScenarioTasks(id, tasks){
		tasks.lastId = this.getLastId(tasks);
		if(id==undefined||id==null||isNaN(id)) currentEditedScenarioTasks = tasks;
		else ScenarioTasksData[id] = tasks;
	},

	getDefaultTasks(){
		return [
		new this.Task(0, "chargement", "load"),
		new this.Task(1, "enregistrement", "save")
		];
	},

	createNewTask(scenarioId, position){
		var tasks = this.getScenarioTasks(scenarioId);
		tasks.lastId += 1;
		var newtask = new this.Task(tasks.lastId, "Nouvelle tche "+tasks.lastId, "undefined");
		tasks.splice(position, 0, newtask);
	},

	deleteTask(scenarioId, task){
		var tasks = this.getScenarioTasks(scenarioId);
		var index = tasks.indexOf(task);
		if(index!=-1) tasks.splice(index, 1);
	},

	deleteScenarioTasks(scenarioId){
		var std = this.get();
		for (var s in std){
			if (std[s] == scenarioId) return delete std[s];
		}
	},

	getLastId(tasksArray){
		var tmp = 0;
		for(var i in tasksArray){
			if(tasksArray[i].id > tmp) tmp = tasksArray[i].id;
		}
		return tmp;
	},
};


