<template>
<div v-on:click="trigger($event)">
	<div class="button-icon"></div>
</div>
</template>

<script>
require('../../assets/buttons/buttonStyle.css');
export default {
	name: 'download-button',
	methods: {
		trigger: function(event){
			this.$emit('click', event);
		}
	}
};
</script>

<style scoped>
	.button-icon {
		background-image: url(../../assets/buttons/download-button.png);
	}
</style>


