<template>
  <div class="new-scenario-page">
    <h2>Nouveau scnario</h2>
    <div class="pannel">
      <h3>Cration du scnario</h3>
      <parameter v-model="name" :param="{id:'name', label:'Nom', inputType:'text'}"></parameter>
      <parameter v-model="description" :param="{id:'description', label:'Description', inputType:'textarea'}"></parameter>
    </div>
    <generic-button @click="nextbutton">SUIVANT</generic-button>
  </div>
</template>

<script>
import Parameter from 'components/run/parameter';
import GenericButton from 'components/buttons/GenericButton';
import ScenarioList from 'model/ScenarioList';
import ScenarioTasks from 'model/ScenarioTasks';

export default {
  name: 'setup-page',
  data () {
    return {
      name:'',
      description:'',
    };
  },

  methods: {
    nextbutton: function(){
      //check if name is not empty
      if (this.name === '') {
        this.$emit("alert", "Le scnario doit avoir un nom.");
      } else {
        //...
        //create new empty scenario and go to edit page
        var scenario = new ScenarioList.Scenario({name:this.name, description:this.description, author:this.$root.$data.username});
        ScenarioList.setCurrentEdited(scenario);
        ScenarioTasks.setScenarioTasks(null, ScenarioTasks.getDefaultTasks());
        //...
        this.$router.push({name:'ScenarioEdit'});
      }
    },
  },

  components: {
    GenericButton, Parameter,
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
  font-size: 1.2rem;
  border-bottom: 1px solid black;
  /*font-variant: small-caps;*/
  text-transform: uppercase;
  padding-bottom: 0.25rem;
}
h3 {
  margin: -0.5rem 0 0.8rem 0;
  padding: 0 0 0.3rem 0;
  font-weight: bold;
  color: #334754;
  font-size: 1rem;
  border-bottom: 1px solid gray;
}
.pannel {
  background-color: white;
  border: 1px solid #334754;
  border-bottom: 3px solid #334754;
  border-right: 2px solid #334754;
  padding: 1rem;
  margin: 1rem 0;
}
</style>



