<template>
  <form class="bg" novalidate
  @submit.prevent="confirm"
  @reset.prevent="cancel">
    <div class="window">
      <section>
        <p><img src="../../assets/pictograms/confirm-icon.png"/></p>
        <p>{{message}}</p>
      </section>
      <footer>
        <button type="submit" class="btn" autofocus><gbutton>OK</gbutton></button>
        <button type="reset" class="btn"><gbutton>ANNULER</gbutton></button>
      </footer>
    </div>
  </form>
</template>

<script>
import gbutton from 'components/buttons/GenericEditorButton';

export default {
  name: 'confirm-window',
  props: ["message"],
  methods: {
    confirm : function(){
      this.$emit('close', true);
    },
    cancel : function(){
      this.$emit('close', false);
    }
  },
  components: { gbutton },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.bg {
  width: 100%;
  height: 100%;
  position: absolute;
  top:0%;
  left:0%;
  background-color: rgba(0,0,0,0.5);
}
.window {
  display: flex;
  flex-direction: column;
  max-width: 80%;
  max-height: 80%;
  width: 25em;
  margin: 3em auto auto;
  background-color: white;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.4);
}
section {
  font-family: Helvetica;
  text-align: center;
  flex-grow: 1;
}
p {
  white-space: pre;
}
img {
  width: 5em;
  height: 5em;
}
footer {
  display: flex;
  align-items: center;
  justify-content: center;
}
.btn {
  margin: 1em 0.5em;
  -webkit-appearance:none;
  -moz-appearance:none;
  appearance:none;
  padding:0;
}
</style>


