<template>
  <div class="file-parameter">
    <label :for="param.id">{{param.label}}</label>
    <div class="inputs-area">
      <select>
        <option disabled value="undefined">No file available</option>
      </select>
      <input
       :id="param.id" 
       value=""
       @input="updateValue($event.target.value)"
       type="file" 
       :name="param.id" />
    </div>
  </div>
</template>

<script>
export default {
  name: 'file-parameter',
  props: ['param', 'value'],
  data () {
    return {
      //description: 'description du scenario'
    }
  },
  methods: {
    updateValue: function(value){
      this.$emit('input', value.trim());
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.file-parameter {
  margin-bottom: 0.5rem;
  display: flex;
  border-bottom: dotted 1px #333333;
}
label {
  flex: 1;
  font-size: 0.9em;
}
.inputs-area{
  flex: 2;
}
select {
  width: 100%;
}
</style>



