<template>
  <div>
    <div class="thead">
      <p>Entre</p><p>Sortie</p>
    </div>
    <div class="tbody">
      <tag-group-item 
      v-for="(tag, index) in tags"
      :key="index"
      :inputs="tag.inputs" 
      :outputs="tag.outputs" 
      :relevance="tag.relevance"
      @removeTag="removeTag(index)"></tag-group-item>
      <a class="addTag" href="#" @click.prevent="addTag"><i></i>Ajouter un lment</a>
    </div>
  </div>

</template>

<script>
import tagItem from 'components/parameters/ConversionTable/TagItem';
import tagGroupItem from 'components/parameters/ConversionTable/TagGroupItem';
import attributeItem from 'components/parameters/ConversionTable/AttributeItem';
import conversionsDatas from 'model/ConversionTable';

export default {
  name: 'conversion-table-parameter',
  props: ['param', 'value'],
  data () {
    return {
      tags: conversionsDatas.get(),
    }
  },
  methods: {
    updateValue: function(value){
      this.$emit('input', value);
    },
    addTag: function(){
      this.tags.push({inputs:['input-tag'],outputs:['output-tag'], relevance:[0]});
    },
    removeTag: function(index){
      this.tags.splice(index, 1);
    }
  },
  components : { tagItem, attributeItem, tagGroupItem, },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.thead{
  display: table;
  width:100%;
  border-collapse:collapse;
}
.thead p {
  display: table-cell;
  text-transform: uppercase;
  text-align: center;
  padding:0.2em;
  color: #334754;
  width: 50%;
  border: 1px solid #B0B0B0;
}
.tbody {
  font-family: monospace;
}
.addTag {
  margin-top: 1em;
  display: flex;
  justify-content: center;
  align-items: center;
}
.addTag i {
  width:1em;
  height: 1em;
  margin-right: 0.5em;
  display: inline-block;
  background: url('../../assets/buttons/editor-add-button.png') no-repeat;
  background-size: 300% 100%;
}
.addTag:hover { opacity: 1; }
.addTag:hover i {background-position: 50%;}
.addTag:active i {background-position: 100%;}

</style>



