<template>
  <div class="scenario-description">
    <div class="top-line">
      <p class="date">Cre le {{scenario.dateCreation.toLocaleString()}}</p>
      <ul class="links">
        <li><router-link :to="{name:'HistoryFor', params:{sid:scenario.id}}">[Voir l'historique]</router-link></li>
        <li><router-link :to="{name:'ScenarioEditExisting', params:{sid:scenario.id}}" v-if="isEditable">[Modifier]</router-link>
        <span v-else class="inactive-link">[Modifier]</span></li>
        <li><router-link :to="{name:'ScenarioSetup', params:{sid:scenario.id}}">[Executer]</router-link></li>
      </ul>
    </div>
    <p class="description">{{scenario.description}}</p>
  </div>
</template>

<script>
export default {
  name: 'scenario-description',
  props: ['scenario', 'isEditable'],
  data () {
    return {
      //description: 'description du scenario'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.scenario-description {
  box-sizing: border-box;
  background-color: white;
  border-bottom: 3px solid #334754;
  border-right: 2px solid #334754;
  border-left: 1px solid #334754;
}
p {
  box-sizing: border-box;
  margin:0;
}
.top-line {
  padding:0.4rem;
  display: flex;
  font-size: 0.9rem;
}
.date {
  flex:1;
}
.links {
  margin:0; padding:0;
  list-style: none;
}
.links li {
  display: inline;
}
.inactive-link {
  opacity: 0.5;
}
.description {
  font-size: 0.95rem;
  border-top: 1px solid #334754;
  margin: 0.1rem 2rem 1rem;
  padding-top: 0.5rem;
}
</style>



