<style scoped>
.scenario-description {
  box-sizing: border-box;
  background-color: white;
  border-bottom: 3px solid #334754;
  border-right: 2px solid #334754;
  border-left: 1px solid #334754;
}
p {
  box-sizing: border-box;
  margin:0;
}
.top-line {
  padding:0.4rem;
  display: flex;
  font-size: 0.9rem;
}
.date {
  flex:1;
}
.description {
  font-family: helvetica;
  border: 1px solid white;
  display: block;
  width: calc(100% - 4rem);
