<template>
<div>
	<div class="button-icon" @click="trigger($event)"></div>
</div>
</template>

<script>
require('../../assets/buttons/buttonStyle.css');
export default {
	name: 'delete-task-button',
	methods: {
		trigger(event){
			this.$emit('click', event);
		},
	},
}
</script>

<style scoped>
.button-icon {
	background-image: url('../../assets/buttons/delete-button.png');
}
</style>


