<template>
  <div class="select-parameter">
    <label :for="param.id">{{param.label}} ({{value}})</label>
    <select 
      :name="param.id"
      :value="value"
      @input="updateValue($event.target.value)">
      <option disabled value="undefined">select something</option>
      <option v-for="option in choices" :value="option">{{option}}</option>
    </select>
  </div>
</template>

<script>
export default {
  name: 'select-parameter',
  props: ['param', 'value', 'choices'],
  data () {
    return {
      //description: 'description du scenario'
    }
  },
  methods: {
    updateValue: function(value){
      this.$emit('input', value);
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.select-parameter {
  margin-bottom: 0.5rem;
  display: flex;
  border-bottom: dotted 1px #333333;
}
label {
  flex: 1;
  font-size: 0.9em;
}
select {
  flex: 2;
}
</style>



